@import "../../scss/variables";

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 1.5rem 0rem;
  margin-bottom: 2rem;

  .card {
    // width: 50%;
    // text-align: center;
    // border: 3px solid red;
    background-color: $cardNavBgColor;
    padding: 1.5rem;
    overflow: hidden;
    // height: 32vh;
    border-radius: 20px;
    box-shadow: -5px 5px 10px 5px rgba(0, 0, 0, 0.8);
    position: relative;
    &:hover img {
      filter: grayscale(100);
      cursor: pointer;
    }

    h1 {
      text-align: start;
      padding: 1rem;
      color: $backgroundColor;
    }

    img {
      width: 500px;
      height: 350px;
      border-radius: 20px;
      object-fit: cover;
    }

    p {
      background-color: rgba(0, 0, 0, 0.5);
      text-align: start;
      color: white;
      position: absolute;
      bottom: 0;
      left: 0;
      font-size: 1.5rem;
      padding: 1.3rem;
      transition: all 0.6s ease-in-out;
      transform: translateY(1000px);
    }

    &:hover p {
      transform: translateY(0px);
      font-weight: bold;
    }
  }
}
